.primer-div {
    color: white;
    background-color: black;
    display: flex;
    justify-content: space-around;
    align-items: center;

    @media (max-width: 768px) {
        flex-direction: column;
    }
}

.home-title {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 40pt;
    padding: 0 0 0 200px;

    @media (max-width: 768px) {
        width: 90%;
        font-size: 30pt;
        text-align: center;
        padding: 2% 20%;
    }
    
    @media (max-width: 480px) {
        font-size: 20pt;
        padding: 7% 10% 0 10%;
    }
}

.card-3d {
    height: 80vh;
    width: 50%;

    @media (max-width: 768px) {
        width: 90%;
    }
    @media (max-width: 480px) {
        
    }
}

.segundo-div {
    background-color: black;
    color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 100vh;

    background-image: url('../assets/background4.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @media (max-width: 768px) {
        flex-direction: column;
    } 
}

.home-items {
    padding: 2% 5%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    line-height: 60px;
    background-color: rgba(0, 0, 0, 0.7);
    border-radius: 10px;
}

.home-item {
    font-size: 30pt;
}

