.the-header {
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    padding: 0 10%;

    @media (max-width: 768px) {
        height: 60px;
    }
    
    @media (max-width: 480px) {
        height: 40px;
    }
}

.logo-image {
    width: 220px;
    padding-top: 15px;

    @media (max-width: 768px) {
        width: 130px;
        padding-top: 20px;
    }
    
    @media (max-width: 480px) {
        width: 90px;
        padding-top: 25px;
    }
}

.button-connect {
    background-color: rgb(50, 50, 50);
    border: 0;
    color: white;
}